.advantage-block {
  background-color: $lightgrey;
  background-size: cover;
}

.advantage-block__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;

  max-width: 1160px;
  padding-top: 120px;
  padding-bottom: 90px;

  @media (max-width: $tablet-width) {
    width: 88.2%;

    max-width: none;

    margin: 0;
    padding-top: 99px;
    padding-bottom: 90px;
  }

  @media (max-width: $mobile-width) {
    display: block;

    width: auto;
    max-width: none;

    margin: auto;
    padding: 0;
    padding-top: 70px;
    padding-bottom: 65px;
  }
}

.advantage-block__wrapper h2 {
  @include default-style;
  @include headtitle-desktop;

  padding-bottom: 61px;

  text-align: center;

  @media (max-width: $tablet-width) {
    @include headtitle-tablet;

    padding-bottom: 42px;
  }

  @media (max-width: $mobile-width) {
    @include headtitle-mobile;

    padding-bottom: 45px;
  }
}

.advantage-block__text {
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;

  width: 1040px;

  margin: auto;

  @media (max-width: $tablet-width) {
    width: 678px;
  }

  @media (max-width: $mobile-width) {
    display: block;
    max-width: none;
    width: auto;

    padding: 0;
    margin: 0;
  }
}

.advantage-block__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-sizing: border-box;

  width: calc(50% - 160px);

  margin: 0;
  margin-bottom: 125px;
  padding-left: 23px;

  @media (max-width: $tablet-width) {
    width: calc(50% - 20px);
    margin-bottom: 0;
    padding-left: 0;
  }

  @media (max-width: $mobile-width) {
    display: block;
    min-width: 290px;

    width: auto;
    padding-left: 0;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 31px;

    text-align: center;

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
    }
  }
}

.advantage-block__item {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  margin-bottom: 24px;

  @media (max-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    display: block;
    min-width: 290px;

    margin: 0 auto;
    padding-left: 0;
    text-align: center;

    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
    }
  }
}

.advantage-block__list--bottom {
  position: relative;

  margin-bottom: 0;

  &::before {
    content: "";
    position: absolute;

    top: -74px;
    left: 6px;

    width: 340px;

    border-bottom: 2px solid rgba(209, 10, 66, 0.2);
  }

  @media (max-width: $tablet-width) {
    &::before {
      display: none;
    }
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 33px;
  }
}

.advantage-block__item--bottom p {
  padding-left: 40px;
  padding-right: 40px;
}

.advantage-block__item p {
  @include default-style;

  display: flex;
  flex-direction: column;

  text-align: left;

  margin-right: 30px;

  @media (max-width: $tablet-width) {
    margin-right: 30px;
  }

  @media (max-width: $mobile-width) {
    text-align: center;
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.advantage-block__number {
  @include default-style;

  font-weight: bold;
  font-size: 71px;
  line-height: 100%;

  background: linear-gradient(130deg, #ed0233 0%, #063db4 45%);
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @media (max-width: $tablet-width) {
    font-weight: bold;
    font-size: 58px;
  }

  @media (max-width: $mobile-width) {
    text-align: center;
  }
}

.advantage-block__unit {
  font-size: 14px;
  line-height: 80%;

  @media (max-width: $mobile-width) {
    text-align: center;
  }
}

.advantage-block__about {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.advantage-block__about h3 {
  @include default-style;

  font-weight: bold;
  font-size: 22px;
  line-height: 150%;

  text-align: left;

  margin-bottom: 5px;

  @media (max-width: $tablet-width) {
    margin-bottom: 0;

    line-height: 135%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 0;

    font-size: 20px;
    line-height: 155%;

    text-align: center;
  }
}

.advantage-block__about p {
  @include default-style;

  width: 98.1%;

  font-weight: 300;
  font-size: 16px;
  line-height: 150%;

  text-align: left;

  @media (max-width: $mobile-width) {
    width: 78.5%;

    margin: auto;

    text-align: center;
    line-height: 150%;
  }
}

.advantage-block__about--square p {
  width: 94%;

  @media (max-width: $tablet-width) {
    width: 93%;
  }

  @media (max-width: $mobile-width) {
    width: 62%;

    margin: auto;
  }
}

.advantage-block__center {
  position: absolute;

  top: 8px;
  bottom: 0;
  left: 50%;

  transform: translateX(-50%);

  display: block;
  box-sizing: border-box;

  max-width: 240px;
  height: 307px;


  padding-top: 46px;
  padding-right: 34px;
  padding-bottom: 30px;
  padding-left: 34px;

  color: $white;
  background-color: $darkblue;

  text-align: center;

  @media (max-width: $tablet-width) {
    position: static;
    transform: none;

    max-width: 442px;
    height: auto;

    margin-top: 40px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 44px;

    padding-top: 20px;
    padding-left: 135px;
    padding-right: 135px;
    padding-bottom: 36px;
  }

  @media (max-width: $mobile-width) {
    position: relative;

    top: 0;
    left: 50%;
    right: 0;
    bottom: 0;

    transform: translateX(-50%);


    max-width: 320px;
    width: 320px;
    min-height: 239px;

    margin: 0;
    margin-top: 45px;
    margin-bottom: 40px;
    padding: 0;
    padding-top: 30px;
    padding-bottom: 45px;
  }
}

.advantage-block__center span {
  @include default-style;

  margin-bottom: 10px;

  font-weight: bold;
  font-size: 74px;
  line-height: 90px;

  color: $white;

  @media (max-width: $tablet-width) {
    line-height: 95px;

    margin-bottom: 0;
  }

  @media (max-width: $mobile-width) {
    text-align: center;
    line-height: 85px;
  }
}

.advantage-block__center h3 {
  @include default-style;

  margin-bottom: 40px;
  font-size: 26px;

  @media (max-width: $tablet-width) {
    font-size: 22px;

    margin-bottom: 17px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 17px;

    text-align: center;
    line-height: 70%;
  }
}

.advantage-block__center p {
  @include default-style;

  margin: 0;

  font-size: 16px;
  line-height: 150%;

  @media (max-width: $mobile-width) {
    padding-left: 70px;
    padding-right: 70px;

    text-align: center;
    line-height: 150%;
  }
}
