.choice-card__list {
  position: relative;

  z-index: 1;

  @include default-style;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;

  max-width: 1160px;

  @media (max-width: $tablet-width) {
    flex-direction: column;

    align-items: center;
    width: auto;

    padding: 0 12.32%;
    margin: 0;
  }

  @media (max-width: $mobile-width) {
    margin: auto;
    padding: 0;
  }
}

.choice-card__item {
  width: 360px;
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }

  @media (max-width: $tablet-width) {
    width: 442px;

    margin-right: 0;
  }

  @media (max-width: $mobile-width) {
    width: auto;

    margin: auto;
    padding: 0;
  }
}

.choice-card__link {
  position: relative;
  z-index: 2;

  @include default-style;

  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;

  width: 100%;

  margin-right: 40px;
  padding-top: 58px;
  padding-right: 77px;
  padding-bottom: 58px;
  padding-left: 77px;

  // transition: all 0.1s;


  background-color: $white;

  border: 7px solid transparent;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    z-index: 5;
    top: -7px;
    right: 0;
    left: -7px;
    bottom: 0;

    width: 346px;
    height: 100%;

    border: 7px solid transparent;
  }

  &:hover,
  &:focus {
    border-image: linear-gradient(111.72deg, #ed0233 17.6%, #003eb7 95.31%);
    border-image-slice: 1;
    // border: none;
    // color: $white;
    outline: none;
  }

  @media (max-width: $tablet-width) {
    width: 442px;

    margin-right: 0;
    margin-bottom: 40px;
    padding-top: 55px;
    padding-right: 118px;
    padding-bottom: 60px;
    padding-left: 118px;

    &::before {
      width: 428px;
    }
  }

  @media (max-width: $mobile-width) {
    width: 290px;

    margin-bottom: 40px;
    padding-top: 40px;
    padding-right: 45px;
    padding-bottom: 45px;
    padding-left: 45px;

    &::before {
      width: 276px;
    }
  }
}

.choice-button {
  @include default-style;

  display: block;
  box-sizing: border-box;

  width: 209px;

  margin-left: -10px;
  padding-top: 25px;
  padding-right: 38px;
  padding-bottom: 24px;
  padding-left: 44px;

  font-family: $font-roboto;
  font-size: 12px;
  line-height: 14px;
  font-weight: bold;
  letter-spacing: 0.05em;

  text-align: center;
  text-transform: uppercase;

  color: $darkblue;
  background-color: transparent;

  border: 2px solid $darkblue;
}

.choice-card__link:hover .choice-button,
.choice-card__link:focus .choice-button {
  padding-top: 27px;
  padding-right: 40px;
  padding-bottom: 26px;
  padding-left: 46px;

  background-image: linear-gradient(111.72deg, #ed0233 17.6%, #003eb7 95.31%);
  border-image-slice: 1;
  border: none;
  color: $white;
  outline: none;
  }

.choice-card__link h3 {
  @include default-style;
  margin-bottom: 11px;

  font-family: $font-roboto;
  font-weight: bold;
  font-size: 26px;
  line-height: 29px;
  text-align: center;

  color: $darkblue;

  @media (max-width: $mobile-width) {
    margin-bottom: 10px;
    font-size: 22px;
    line-height: 110.16%;
  }
}

.choice-card__time {
  @include default-style;
  margin-bottom: 10px;

  font-family: $font-roboto;
  font-size: 16px;
  line-height: 150%;
  text-align: center;

  color: #495a7d;

  @media (max-width: $mobile-width) {
    margin-bottom: 10px;
  }
}

.choice-card__shadow {
  margin-bottom: 28px;

  font-family: $font-roboto;
  font-weight: bold;
  font-size: 100px;
  line-height: 150%;
  text-align: center;

  color: #e8e8e8;

  opacity: 0.48;

  @media (max-width: $mobile-width) {
    margin-bottom: 25px;
    font-size: 80px;
  }
}

.choice-card__prise {
  position: absolute;

  @include default-style;

  left: 50%;
  top: 43%;
  z-index: 5;
  transform: translateX(-50%);

  font-family: $font-roboto;
  font-weight: bold;
  font-size: 60px;
  line-height: 150%;
  text-align: center;

  color: #103aae;

  @media (max-width: $mobile-width) {
    top: 42%;

    font-size: 47px;
  }
}

.choice-card__code {
  color: $darkblue;
}
