.reviews-block {
  background-color: $white;
}

.reviews-block__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;

  max-width: 560px;

  padding-bottom: 120px;

  @media (max-width: $tablet-width) {
    padding-bottom: 100px;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 70px;
  }
}

.reviews-block__wrapper h2 {
  @include default-style;
  @include headtitle-desktop;

  margin-bottom: 50px;

  text-align: center;

  @media (max-width: $tablet-width) {
    @include headtitle-tablet;

    margin-bottom: 40px;
  }

  @media (max-width: $mobile-width) {
    @include headtitle-mobile;
  }
}

.reviews-block__client {
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  box-sizing: border-box;

  min-height: 242px;

  padding-top: 55px;
  padding-right: 60px;
  padding-bottom: 55px;
  padding-left: 60px;

  background-color: $darkblue;

  @media (max-width: $tablet-width) {
    padding-top: 54px;
  }

  @media (max-width: $mobile-width) {
    display: block;

    width: 226px;
    min-height: 336px;

    margin-left: 0;
    margin-right: 0;
    padding-top: 53px;
    padding-right: 26px;
    padding-bottom: 33px;
    padding-left: 26px;
  }
}

.reviews-block__img {
  width: 80px;
  height: auto;

  margin-right: 30px;
  padding-top: 8px;

  @media (max-width: $mobile-width) {
    position: absolute;
    top: 32px;
    left: 40px;
    width: 68px;
  }
}

.reviews-block__img img {
  max-width: 80px;
  border-radius: 50%;

  @media (max-width: $mobile-width) {
    width: 68px;
    height: 68px;
  }
}

.reviews-block__text h3 {
  @include default-style;

  font-family: $font-roboto;
  font-weight: bold;
  font-size: 20px;
  line-height: 150%;

  margin-bottom: 14px;
  text-align: left;

  color: $white;

  @media (max-width: $tablet-width) {
    margin-bottom: 9px;
  }

  @media (max-width: $mobile-width) {
    position: absolute;

    top: 48px;
    left: 2px;
    text-align: left;

    margin-bottom: 20px;
    padding-left: 120px;
  }
}

.reviews-block__text p {
  @include default-style;

  font-family: $font-roboto;
  font-size: 16px;
  line-height: 140%;

  text-align: left;

  color: $white;

  @media (max-width: $mobile-width) {
    margin-top: 70px;
    text-align: center;
  }
}
